	module.exports = {"data":{"markdownRemark":{"html":"<p>後半がちょい蛇足気味かなぁ…まぁいいか。</p>\n<p>そんなわけで大変お待たせしました。<a href=\"/!/thC/\">東方夢終劇R2.00パッチ</a>を公開します。\n有料パッチ？貴様は何処でそんな法螺吹き込まれたんだ？<br>\n主に<strong>おまけスペル30こ弱</strong>と<em>リザルト閲覧機能</em>追加、あとは一部画面演出の調整諸々。\n(それ以外の小さな更新点は<a href=\"/!/thC/\">夢終劇のページ</a>を見てくださいな)</p>\n<p>とりあえずできたよーってことで速報まで。 それと<a href=\"/3571\">東方イラスト</a>1件追加。ミルククローゼットな紫さん。 (↑言うまでも無くパッチ投下が遅れた主原因)</p>\n<h2>近いうちにサイトリニューアル工事始めます</h2>\n<p>当サイトは(支配人が手抜きしたいため)全体の8割以上がXML形式で構成されているので一部のブラウザだと見れねーぞｺﾞﾙｧとかしばしば意見がくるので、まぁ他の理由もひっくるめて当サイト全体をリニューアルすることに決めました。</p>\n<h2>どう変わるん？</h2>\n<ul>\n<li>パッと見デザインは殆ど変わりません。寧ろ意図的に変えないようにします。</li>\n<li>RSS機能や各記事ごとのコメント入力機能とトラックバックを実装します。要はBlog化です。</li>\n<li>今度はちゃんとサーバサイドでHTML形式に咀嚼して吐き出します。おぇ</li>\n<li>実は内部的にPerlとPgSQLが動いています。</li>\n<li>Web拍手もどきを強化し、掲示板はツリー式に置き換えてサポート専用とします。</li>\n<li>今インしてるお機能</li>\n</ul>","frontmatter":{"cover":null,"date":"2007-05-05T03:38:00+09:00","strDate":"2007/5/4","title":"やよい日の月満ちし時あらはれむ くるひ合ひたるもののあやかし"}}},"pathContext":{"path":"/ja/blog/2007/05/05-renewal/","langKey":"ja"}}

